* {
    font-family: 'Montserrat', sans-serif;
}
body {
    width: 100%;
    padding :0;
    margin : 0;
}

a:link {
  color: skyblue;
  background-color: transparent;
  text-decoration: none;
}

a:visited {
  color: green;
  background-color: transparent;
  text-decoration: none;
}

a:hover {
  color: orange;
  background-color: transparent;
  text-decoration: underline;
}

a:active {
  color: yellow;
  background-color: transparent;
  text-decoration: underline;
}
